*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: scrollbar;
  -webkit-tap-highlight-color: transparent;
}

@-ms-viewport {
  width: device-width;
}

article, aside, dialog, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  text-align: left;
  background-color: #fff;
}

[tabindex="-1"]:focus {
  outline: none !important;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

abbr[title],
abbr[data-original-title] {
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
  cursor: help;
  border-bottom: 0;
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0;
}

blockquote {
  margin: 0 0 1rem;
}

dfn {
  font-style: italic;
}

b,
strong {
  font-weight: bold;
}

small {
  font-size: 80%;
}

sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

a {
  color: #007bff;
  text-decoration: none;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:hover {
  color: #0056b3;
  text-decoration: underline;
}

a:not([href]):not([tabindex]) {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus, a:not([href]):not([tabindex]):hover {
  color: inherit;
  text-decoration: none;
}

a:not([href]):not([tabindex]):focus {
  outline: 0;
}

pre,
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

pre {
  margin-top: 0;
  margin-bottom: 1rem;
  overflow: auto;
  -ms-overflow-style: scrollbar;
}

figure {
  margin: 0 0 1rem;
}

img {
  vertical-align: middle;
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

a,
area,
button,
[role="button"],
input:not([type="range"]),
label,
select,
summary,
textarea {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}

table {
  border-collapse: collapse;
}

caption {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  color: #868e96;
  text-align: left;
  caption-side: bottom;
}

th {
  text-align: inherit;
}

label {
  display: inline-block;
  margin-bottom: .5rem;
}

button {
  border-radius: 0;
}

button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box;
  padding: 0;
}

input[type="date"],
input[type="time"],
input[type="datetime-local"],
input[type="month"] {
  -webkit-appearance: listbox;
}

textarea {
  overflow: auto;
  resize: vertical;
}

fieldset {
  min-width: 0;
  padding: 0;
  margin: 0;
  border: 0;
}

legend {
  display: block;
  width: 100%;
  max-width: 100%;
  padding: 0;
  margin-bottom: .5rem;
  font-size: 1.5rem;
  line-height: inherit;
  color: inherit;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: none;
}

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

output {
  display: inline-block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none !important;
}

@font-face {
  font-family: "Bermina Sans";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/BerninaSans-Web-CondensedBold.woff2") format("woff2");
}

.inner {
  width: 100%;
}

.bg_dotted {
  background-image: url("../img/points.svg");
}

.bg_orange {
  background-color: orange;
}

.bg_vert {
  background-color: green;
}

.lesLogos {
  display: flex;
  flex-wrap: wrap;
}

.lesLogos > div {
  width: 50%;
}

@media screen and (min-width: 480px) {
  .inner {
    width: 480px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .inner {
    width: 768px;
  }
}

.texte_principal {
  text-align: center;
  font-weight: bold;
}

.photo_texte {
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}

body {
  font-family: "Bermina Sans";
  color: #00465b;
}

h3 {
  font-family: "GothamHTF";
}

img {
  width: 100%;
  height: auto;
}

header {
  background-image: url("../background.jpg");
  height: 400px;
  width: 100%;
  position: relative;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: flex-end;
}

.promo {
  color: white;
  background-color: red;
  border-radius: 5px;
  position: absolute;
  top: 4em;
  right: 2em;
  transform: rotate(25deg);
  width: 200px;
  padding: 2%;
}

header > div {
  width: 100%;
}

.text_background {
  font-size: 0.5em;
  background-color: rgba(255, 255, 255, 0.2);
  margin-bottom: 10px;
}

.temperature {
  font-size: 0.8em;
}

.text_background div {
  color: white;
}

.btn {
  border: solid black 1px;
  border-radius: 5px;
}

.rdv {
  background: #fff;
  color: #00465b;
}

.hautDePage {
  display: flex;
  justify-content: space-between;
}

.bouton {
  margin-top: auto;
  margin-bottom: auto;
}

.hidden {
  visibility: hidden;
}

.text_background {
  display: flex;
  flex-direction: column;
}

.center {
  text-align: center;
  margin-top: auto;
  margin-bottom: auto;
}

.textcenter {
  text-align: center;
}

.photo img {
  border-radius: 50%;
}

@media screen and (min-width: 480px) {
  .logo_site {
    width: 200px;
  }
  .photos {
    display: flex;
    flex-wrap: wrap;
  }
  .photos > div {
    width: 50%;
  }
  .image_smartphone {
    order: 2;
  }
  .image_gym {
    order: 1;
  }
  .image_cryolipolyse {
    order: 3;
    margin: auto;
  }
}

@media screen and (min-width: 768px) {
  .photos {
    display: flex;
    flex-wrap: nowrap;
  }
  .photos > div {
    width: 1;
  }
  header {
    height: 800px;
  }
  .text_background {
    display: flex;
    flex-direction: row;
    font-size: 1em;
  }
  .nomMarque {
    display: flex;
    justify-content: flex-end;
  }
}

